import axios from "axios";

export const getQuestionaireByFixtureId = (fixtureId) => {
  return axios.get(
    import.meta.env.VITE_API_URI + `api/v1/questionaires/${fixtureId}`
  );
};

export const setPrediction = (data) => {
  return axios.post(
    import.meta.env.VITE_API_URI + `api/v1/prediction`,{
      "data": data
    }
  );
};

export const getUserPredictions = (userId) => {
  return axios.get(
    import.meta.env.VITE_API_URI + `api/v1/prediction?userid=${userId}`
  );
};

export const getAllPredictions = () => {
  return axios.get(import.meta.env.VITE_API_URI + `api/v1/prediction`);
}

export const getAllPredictionsByFixture = (f_id) => {
  return axios.get(import.meta.env.VITE_API_URI + `api/v1/prediction?fixtureid=${f_id}`);
}

export const getPredictionById = (pid) => {
  return axios.get(import.meta.env.VITE_API_URI + `api/v1/predictionbyid/${pid}`);
}


